@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css");
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500;600;700&display=swap');
@import url("defaults.css");

$yldGrey: #181b1e;
$yldGold: #efbc86;
$green: #59d379;
$blue: #5cb4ff;
$orange: #ff9d57;
$red: #f84a4a;

$black: #121212;
$highlightBlue: #3b88fd;
$lightGrey: #dcdcdc;

$sans: 'Inter', sans-serif;
$roboto: 'Roboto Mono', monospace;

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-numeric: tabular-nums;
  font-size: 16px;
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(24,24,24,1) 100%);
  background-repeat: no-repeat;

  @media screen and (max-width: 767px){
    font-size: 14px;
  }
}

body {
  display: flex;
  flex:0 0 auto;
  flex-direction: column;
  align-items: center;

  font-weight: 400;
  font-family: $sans;
  color: $lightGrey;
}

#wrapper {
  max-width: 1200px;
  padding: 2rem 1.25rem;
}

a {
  text-decoration: none;
}
h1, h2, h3,
h4, h5, h6, p {
  margin: 0;
}

.ribbons {
  position: relative;

  &.reverse {
    &:after {
      transform: translate(-50%,0%) scale(-1,0.4);
    }
    &:before {
      transform: translate(-50%,0%) scale(1,0.35);
    }
  }

  &:after {
    content: '';
    background-image: url("../img/curvey-prewarp.svg");
    position: absolute;
    height: 62vw;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    bottom: -25vw;
    left: 50%;
    transform: translate(-50%,0%) scale(1,0.4);
    opacity: 0.65;
    z-index: -1;
  }

  &:before {
    content: '';
    background-image: url("../img/curvey-prewarp.svg");
    position: absolute;
    height: 62vw;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    bottom: -26vw;
    left: 50%;
    transform: translate(-50%,0%) scale(-1,0.35);
    opacity: 0.25;
    z-index: -1;
  }
}

header {
  .topNav {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: 600px){
      justify-content: center;
    }

    &__list {
      display: flex;
      align-items: center;
    }

    .topNav__list__item {

      a {
        color: $lightGrey;
        text-decoration: none;
        padding: 0 1rem;

        @media screen and (max-width: 600px){
          padding: 0 0.75rem;
        }
      }

      &__logo {
        img {
          width: 3rem;
        }

        @media screen and (max-width: 600px){
          display: none;
        }
      }
      &--buy {
        a {
          color: $yldGold;
        }

        @media screen and (max-width: 600px){
          display: none;
        }
      }
    }

    &__btn {
      color: $lightGrey;
      font-weight: 500;
      font-size: 0.8rem;
      padding: 0.625rem 1.5rem;
      border: 1px solid $yldGold;
      background: rgba($yldGold,0.1);
      border-radius: 0.25rem;
      text-transform: uppercase;
      display: inline-block;

      @media screen and (max-width: 600px){
        display: none;
      }
    }
  }
}

main {
  .hero {
    padding: 6rem 0;
    display: grid;
    grid-template-columns: 3.5fr 2fr;
    grid-gap: 4rem;
    align-items: center;
    position: relative;

    #curvey {
      position: absolute;
      height: 62vw;
      width: 100vw;
      bottom: -25vw;
      left: 50%;
      transform: translate(-50%,0%) scale(1,0.4);
      opacity: 0.65;
      z-index: -1;
      fill: url(#curveyGrad);
    }

    @media screen and (max-width: 600px){
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      padding: 2rem 0 0 0;
    }

    &__textArea {
      &__headings {
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;

        @media screen and (max-width: 600px){
          grid-template-columns: auto auto;
        }

        &__h1 {
          color: #fff;
          font-weight: 700;
          font-size: 4.5rem;
          @media screen and (max-width: 600px){
            font-size: 3.25rem;
            display: flex;
          }
        }
        &__h2 {
          font-weight: 300;
          font-size: 2rem;
          color: $yldGold;
          @media screen and (max-width: 600px){
            font-size: 1.75rem;
          }
        }
        img {
          display: none;
          max-height: 3.25rem;
          margin-left: 1rem;

          @media screen and (max-width: 600px){
            display: inline-block;
          }
        }
      }
      &__p {
        font-size: 1.25rem;
        line-height: 2rem;
        margin: 3rem 0;

        @media screen and (max-width: 600px){
          font-size: 1rem;
          line-height: 1.5rem;
          margin: 2rem 0;
        }
      }
      &__btn-wrapper {
        display: flex;
        text-align: center;

        > *:first-child {
          margin-right: 1rem;
        }
      }
      &__btn {
        color: $black;
        font-weight: 500;
        font-size: 1.125rem;
        padding: 1rem 2rem;
        border: 1px solid $yldGold;
        background: rgba($yldGold,0.95);
        border-radius: 0.3125rem;
        text-transform: uppercase;

        &--buy {
          display: none;
          color: $black;
          font-weight: 500;
          font-size: 1.125rem;
          padding: 1rem 2rem;
          border: 1px solid $lightGrey;
          background: rgba(#fff,0.95);
          border-radius: 0.3125rem;
          text-transform: uppercase;
          display: inline-block;

          @media screen and (max-width: 600px){
            display: block;
          }
        }

        @media screen and (max-width: 600px){
          font-size: 1rem;
        }
      }
    }
    &__phone {
      text-align: center;

      #svgIcon {
        fill: $yldGold;
        fill: url(#grad1);
        filter:url(#dropshadow);
      }
      .hidden {
        visibility: hidden;
      }
      #slider {
        max-width: 300px;
        margin: 20px auto;
        background: transparent;
        height: 0.5rem;
        border: 1px solid rgba(#fff,0.25);
        display: none;

        span.ui-slider-handle {
          border-radius: 50%;
          background: $yldGold;
          border: none;
          top: -0.5rem;
        }
      }
      img {
        max-width: 100%;
        display: none;
      }
      @media screen and (max-width: 600px){
        grid-row: 1;

        img {
          max-width: 65%;
          display: block;
          margin: 0 auto;
        }
      }
    }
  }

  section.content {
    padding: 6rem 0;

    @media screen and (max-width: 767px){
      padding: 3rem 0;
    }
  }

  .features {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2rem;
    align-items: flex-start;

    @media screen and (max-width: 767px){
      grid-template-columns: 1fr;
      grid-gap: 3rem;
    }

    &__item {
      svg {
        height: 4.5rem;
        fill: $yldGold;

        @media screen and (max-width: 600px){
          height: 3.5rem;
          display: none;
        }
      }
      &__name {
        color: $yldGold;
        font-size: 1.5rem;
        font-weight: 500;
        margin: 1.5rem 0;
      }
      &__description {
        line-height: 1.5rem;
      }
    }
  }

  .individualized {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 4rem;

    @media screen and (max-width: 767px){
      grid-template-columns: 1fr;
      grid-gap: 3rem;
    }

    &__text {
      p {
        line-height: 1.5rem;

        &:first-child {
          margin-bottom: 1.5rem;
        }
      }

      @media screen and (max-width: 767px){
        grid-row: 2;
      }
    }
    &__graph {
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      justify-content: center;
      align-items: center;
      text-align: center;

      @media screen and (max-width: 767px){
        grid-row: 1;
      }

      &__name {
        font-weight: 500;
      }

      &__legend {
        display: flex;
        justify-content: center;

        &__item {
          display: grid;
          grid-template-columns: auto auto;
          align-items: center;
          grid-gap: .75rem;

          &:first-child {
            margin-right

            : 3rem;
          }

          &__dot {
            width: 2rem;
            height: 2rem;
            border-radius: 50%;

            &--gold {
              background: $yldGold;
            }
            &--grey {
              background: #555;
            }
          }
        }
      }
    }
  }

  .rewards {
    &__text {
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      text-align: justify;
      text-align-last: center;
      max-width: 800px;
      margin: 0 auto;

      @media screen and (max-width: 767px){
        text-align: left;
        text-align-last: left;
      }

      &__heading {
        font-size: 2rem;
        font-weight: 500;
        color: $yldGold;
      }
      &__body {
        line-height: 1.5rem;
      }
    }
  }
}
